const Constants = require('./Constants');

class ServerCore {
    constructor(io) {
        this.io = io;
        this.initSocketListener();
    }

    initSocketListener() {
        this.io.on('connection', (socket) => {

            global.console.log('player connected');

            // 反馈连接消息
            this.io.sockets.emit(Constants.SEND_ALL_CONNECTED, 'new player connected!');
            socket.emit(SEND_ONE_CONNECTED, JSON.stringify({ message: 'connected', data: '' }));

            // 监听接收玩家断开连接消息
            socket.on(Constants.LISTEN_DISCONNECT, (data) => {
                
            });

            // 监听接收玩家消息
            socket.on(Constants.LISTEN_MESSAGE, (data) => {
                
            });

            // 监听接收玩家准备信息
            socket.on(Constants.LISTEN_READY, (data) => {
                
            });

            // 监听玩家抢地主信息
            socket.on(Constants.LISTEN_COMPETE, (data) => {
                
            });

            // 监听玩家出牌信息
            socket.on(Constants.LISTEN_DEAL, (data) => {

            });

        });
    }
}

module.exports = ServerCore;